
.stars {
  display: flex;
  align-items: center;
}

.stars__star {
  background-image: url(images/star.svg);
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  margin-right: 4px;
  background-position: center;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n + 4) {
  background-image: url(images/star-active.svg);
} 
